.loader-wrapper {
  position: fixed;
  z-index: 999999;
  background: #fff;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.loader-wrapper .dot {
  position: relative;
  width: 22px;
  height: 22px;
  margin: 8px;
  border-radius: 50%;
}
.loader-wrapper .dot::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background: inherit;
  border-radius: inherit;
  animation: wave 2s ease infinite;
}
.loader-wrapper .dot:nth-child(1) {
  background: rgba(59, 124, 245, 0.2);
}
.loader-wrapper .dot:nth-child(1)::before {
  animation-delay: 0.2s;
}
.loader-wrapper .dot:nth-child(2) {
  background: rgba(245, 52, 103, 0.4);
}
.loader-wrapper .dot:nth-child(2)::before {
  animation-delay: 0.4s;
}
.loader-wrapper .dot:nth-child(3) {
  background: rgba(59, 124, 245, 0.6);
}
.loader-wrapper .dot:nth-child(3)::before {
  animation-delay: 0.6s;
}
.loader-wrapper .dot:nth-child(4) {
  background: rgba(245, 52, 103, 0.8);
}
.loader-wrapper .dot:nth-child(4)::before {
  animation-delay: 0.8s;
}
.loader-wrapper .dot:nth-child(5) {
  background: #3B7CF5;
}
.loader-wrapper .dot:nth-child(5)::before {
  animation-delay: 1s;
}
@keyframes wave {
  50%, 75% {
    transform: scale(2.5);
  }
  80%, 100% {
    opacity: 0;
  }
}

.loader-box {
  height: 150px;
  text-align: center;
  display: flex;
  align-items: center;
  vertical-align: middle;
  justify-content: center;
  transition: 0.3s color, 0.3s border, 0.3s transform, 0.3s opacity;
}
.loader-box [class*=loader-] {
  display: inline-block;
  width: 50px;
  height: 50px;
  color: inherit;
  vertical-align: middle;
}
.loader-box .loader-1 {
  border: 0.2em dotted #3B7CF5;
  border-radius: 50%;
  animation: 1s loader-01 linear infinite;
}
@keyframes loader-01 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loader-box .loader-2 {
  border: 0.2em solid transparent;
  border-left-color: #F53467;
  border-right-color: #F53467;
  border-radius: 50%;
  animation: 1s loader-02 linear infinite;
}
@keyframes loader-02 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loader-box .loader-3 {
  border: 0.2em solid #61ae41;
  border-bottom-color: transparent;
  border-radius: 50%;
  animation: 1s loader-03 linear infinite;
  position: relative;
}
@keyframes loader-03 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loader-box .loader-4 {
  border: 1px solid #f81f58;
  border-radius: 50%;
  animation: 1s loader-04 linear infinite;
  position: relative;
}
.loader-box .loader-4:before {
  content: "";
  display: block;
  width: 0;
  height: 0;
  position: absolute;
  top: -0.2em;
  left: 50%;
  border: 0.2em solid #f81f58;
  border-radius: 50%;
}
@keyframes loader-04 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loader-box .loader-5 {
  border: 0.2em solid transparent;
  border-top-color: #e6ae30;
  border-radius: 50%;
  animation: 1s loader-05 linear infinite;
  position: relative;
}
.loader-box .loader-5:before {
  content: "";
