  position: absolute;
  content: "";
  top: -3px;
  right: -1px;
  background-color: var(--theme-deafult);
  width: 10px;
  height: 10px;
  border-radius: 100%;
  box-shadow: 0 0 17px 4px rgba(var(--theme-deafult), 0.36);
}
