  display: block;
  position: absolute;
  top: -5rem;
  left: 1.25rem;
  color: #2c323f;
  font-family: "Montserrat", sans-serif;
  font-size: 22px;
}
.timeliny .timeliny-dot::after {
  transition: all 0.35s ease-in-out;
  transform: translateY(-50%);
  content: attr(data-text);
  display: block;
  position: absolute;
  width: 256px;
  top: 5.6rem;
  left: 1.25rem;
  color: #f4f4f4;
  opacity: 0;
